// 作用等同 vw, $minWidth 以下不再縮小
@function vw($width: 1, $minWidth: 360px){
  @return calc(#{$width} * max(100vw, #{$minWidth})/100)
};

// $base 寬度為 $width px 等比例縮放, $min 最小值, $minWidth 以下不再縮小
@function pxw($width: 1, $min: 1px, $base: 1920, $minWidth: 360px){
  @return calc(max(#{$width} * max(100vw, #{$minWidth})/#{$base}, #{$min}))
};