@import '../../styles/var';
@import '../../styles/function';

.corn-popup-group,
.corn-popup-overlay {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

// popup 大容器
.corn-popup-group {
  position: fixed;
  z-index: 999;
  &,
  & * {
    // font-family: HelveticaNeue;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.23;
    letter-spacing: normal;
    text-align: center;
  }
  // &.show {
  //   pointer-events: none;
  // }
  &.hide {
    pointer-events: none;
  }

  & > .corn-popup-overlay  {
    position: absolute;
  }
}

// popup 後方遮罩
.corn-popup-overlay {
  position: fixed;
  display: flex;
  z-index: 999;
  background-color: rgba($uerboardTextBgColor, 0.5);
  transition: opacity 0.5s ease-in-out;
  opacity: 0;
  &.show {
    opacity: 1;
    .corn-popup-container {
      // transform: translate3d(0, 0, 0);
    }
  }
  &.hide {
    opacity: 0;
    pointer-events: none;
    .corn-popup-container {
      transform: translate3d(0, -20px, 0);
    }
  }

  // popup 框
  .corn-popup-container {
    position: relative;
    display: flex;
    flex-direction: column;
    margin: auto;
    padding: 36px 28px;
    border-radius: 20px;
    box-sizing: border-box;
    // border: solid 1px $redColor;
    background-color: $uerboardTextBgColor;
    transition: transform 0.5s ease-in-out;
    max-width: calc(100% - 56px);
    max-height: calc(100% - 56px);
  }

  // 標題
  .corn-popup-title {
    margin: 0 auto 18px auto;
    height: 26px;
    font-size: 22px;
    line-height: 1.23;
    // color: $redColor;
  }

  // 副標
  .corn-popup-sub {
    margin: 18px auto 40px auto;
    font-size: 20px;
    // color: $uerboardTextGrayColor;
  }

  // 內容區塊
  .corn-popup-content {
    width: stretch;
    // border-top: solid 1px $redColor;
    margin-bottom: 30px;
    white-space: pre-line;
    max-height: calc(100% - 56px);
    overflow-y: scroll;
  }

  // 底部
  .corn-popup-bottom {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    .corn-popup-button {
      margin: 0 10px;
    }
  }

  &.is-default-type {
    .corn-popup-container {
      border: solid 1px $mainColor;
    }
    .corn-popup-sub {
      color: $uerboardTextGrayColor;
    }
    .corn-popup-title {
      color: $mainColor;
    }
    .corn-popup-content {
      border-top: solid 1px $mainColor;
    }
  }

  &.is-alert-type {
    .corn-popup-container {
      border: solid 1px $redColor;
    }
    .corn-popup-sub {
      color: $uerboardTextGrayColor;
    }
    .corn-popup-title {
      color: $redColor;
    }
    .corn-popup-content {
      border-top: solid 1px $redColor;
    }
  }
}
